/**
 * External Dependencies
 */
/** @jsx jsx */
import { jsx, css } from '@emotion/react';

/**
 * WordPress Dependencies
 */
import {
	SearchControl,
	TextControl,
	Spinner,
	NavigableMenu,
	KeyboardShortcuts,
} from '@wordpress/components';
import apiFetch from '@wordpress/api-fetch';
import { Fragment, useState, useRef, useMemo, useEffect } from '@wordpress/element'; // eslint-disable-line
import { __ } from '@wordpress/i18n';
import { useDebounce } from '../../hooks';
import { cleanForSlug } from '@wordpress/url';

/**
 * Internal Dependencies
 */
import SearchItem from './search-item';

const NAMESPACE = 'prc-content-search';

// Establish a cache for search results by search query.
const searchCache = {};

function ContentSearch({
	onSelectItem,
	placeholder,
	label,
	style = 'search',
	contentTypes,
	mode,
	excludeItems,
	perPage,
}) {
	const [searchInput, setSearchInput] = useState('');
	const searchString = useDebounce(searchInput, 500);
	const searchStringIsUrl = useMemo(() => {
		if (searchString.match(/^(http|https):\/\//)) {
			return true;
		}
		return false;
	}, [searchString]);

	const [searchResults, setSearchResults] = useState([]);
	const [isLoading, setIsLoading] = useState(false);
	const [selectedItem, setSelectedItem] = useState(null);

	const abortControllerRef = useRef();
	const mounted = useRef(true);

	const handleSearchResults = (results, searchQuery = false) => {
		abortControllerRef.current = null;

		if (false !== searchQuery) {
			searchCache[searchQuery] = results;
		}

		const toSet = results.filter((result) => {
			let keep = true;

			if (excludeItems.length) {
				keep = excludeItems.every((item) => item.id !== result.id);
			}

			return keep;
		});

		setSearchResults(toSet);

		setIsLoading(false);
	};

	/**
	 * handleSelection
	 *
	 * update the selected item in state to either the selected item or null if the
	 * selected item does not have a valid id
	 *
	 * @param {*} item
	 */
	const handleOnNavigate = (item) => {
		if (0 === item) {
			setSelectedItem(null);
		}

		setSelectedItem(item);
	};

	const getPostByUrl = (url) => {
		if (undefined === url) {
			return new Error('url is undefined');
		}
		return new Promise((resolve, reject) => {
			apiFetch({
				path: '/prc-api/v2/utils/get-post-by-url',
				method: 'POST',
				data: { url },
			})
				.then((post) => {
					console.log('getPostByUrl', url, post);
					if ('object' !== typeof post) {
						reject(new Error('post is not an object'));
					}
					resolve(post);
				})
				.catch((err) => reject(err));
		});
	};

	/**
	 * handleItemSelection
	 *
	 * reset the search input & item container
	 * trigger the onSelectItem callback passed in via props
	 *
	 * @param {*} item
	 */
	const handleItemSelection = (item) => {
		setSearchResults([]);
		setSearchInput('');
		onSelectItem(item);
	};

	const hasSearchString = !!searchString.length;
	const hasSearchResults = !!searchResults.length;

	/**
	 * handleSearchStringChange
	 *
	 * Using the keyword and the list of tags that are linked to the parent block
	 * search for posts/terms that match and return them to the autocomplete component.
	 *
	 * @param {string} keyword search query string
	 */
	const handleSearchStringChange = (keyword, isUrl = false) => {
		if (abortControllerRef.current) {
			abortControllerRef.current.abort();
		}

		// If the keyword is empty, reset the search results and return.
		if ('' === keyword.trim()) {
			console.log('Resetting search string...');
			handleSearchResults([]);
			return;
		}

		abortControllerRef.current = new AbortController();

		setIsLoading(true);

		const searchQuery = `wp/v2/search/?search=${keyword}&subtype=${contentTypes.join(
			',',
		)}&type=${mode}&_embed&per_page=${perPage}`;

		// Check search cache for the searchQuery and if present then return the cached results.
		if (searchCache[searchQuery]) {
			handleSearchResults(searchCache[searchQuery], searchQuery);
		} else if (isUrl) {
			console.log('isUrl', keyword);
			getPostByUrl(keyword)
				.then((post) => {
					if (mounted.current) {
						console.log('parse getPostByUrl', post);

						const { ID, post_title, post_type, permalink, label } = post;

						handleSearchResults(
							[
								{
									id: ID,
									subtype: post_type,
									title: post_title,
									type: 'post',
									url: permalink,
									label,
								},
							],
							searchQuery,
						);
					}
				})
				.catch((error, code) => {
					console.log(
						'Hey this is a url error, we didnt find a matching post... now what?',
						keyword,
					);
					console.error(error, code);
					// Stop spinning.
					setIsLoading(false);
				});
		} else {
			apiFetch({
				path: searchQuery,
				signal: abortControllerRef.current.signal,
			})
				.then((results) => {
					if (false === mounted.current) {
						return;
					}

					console.log(
						'Not a url, searching.... here are the results:',
						results,
					);

					handleSearchResults(results, searchQuery);
				})
				.catch((error, code) => {
					// fetch_error means the request was aborted
					if ('fetch_error' !== error.code) {
						handleSearchResults([]);
					}
				});
		}
	};

	useEffect(
		() => () => {
			mounted.current = false;
		},
		[],
	);

	useEffect(() => {
		if ('' !== searchString) {
			handleSearchStringChange(searchString, searchStringIsUrl);
		}
	}, [searchString, searchStringIsUrl]);

	const listCSS = css`
		/* stylelint-disable */
		max-height: 350px;
		overflow-y: auto;
	`;

	return (
		<NavigableMenu onNavigate={handleOnNavigate} orientation="vertical">
			<div
				style={{
					display: 'flex',
				}}
			>
				<div
					style={{
						flexGrow: '1',
					}}
				>
					{'minimal' === style && (
						<TextControl
							label={label}
							value={searchInput}
							onChange={(keyword) => setSearchInput(keyword)}
							placeholder={placeholder}
							autoComplete="off"
						/>
					)}
					{'search' === style && (
						<SearchControl
							value={searchInput}
							onChange={(keyword) => setSearchInput(keyword)}
							placeholder={placeholder}
						/>
					)}
				</div>

				{isLoading && (
					<div
						style={{
							position: 'absolute',
							right: 'minimal' === style ? '8px' : '50px',
							marginTop: 'minimal' === style ? '27px' : '10px',
						}}
					>
						<Spinner />
					</div>
				)}
			</div>
			{hasSearchString ? (
				<ul
					className={`${NAMESPACE}-list`}
					style={{
						marginTop: '0',
						marginBottom: '0',
						marginLeft: '0',
						paddingLeft: '0',
						listStyle: 'none',
					}}
					css={listCSS}
				>
					{!isLoading && !hasSearchResults && false === searchStringIsUrl && (
						<li
							className={`${NAMESPACE}-list-item components-button`}
							style={{
								color: 'inherit',
								cursor: 'default',
								paddingLeft: '3px',
							}}
						>
							{__('Nothing found.', 'prc-block-components')}
						</li>
					)}
					{!isLoading && !hasSearchResults && searchStringIsUrl && (
						<li
							className={`${NAMESPACE}-list-item components-button`}
							style={{
								color: 'inherit',
								cursor: 'default',
								paddingLeft: '3px',
							}}
						>
							{__('Press enter to change url.', 'prc-block-components')}
						</li>
					)}
					{!isLoading &&
						searchResults.map((item, index) => {
							console.log('searchResults map...', item);
							if (undefined === item.title) {
								return null;
							}

							return (
								<li
									key={item.id}
									className={`${NAMESPACE}-list-item`}
									style={{
										marginBottom: '0',
									}}
								>
									<SearchItem
										onClick={() => handleItemSelection(item)}
										searchTerm={searchString}
										suggestion={item}
										contentTypes={contentTypes}
										isSelected={selectedItem === index + 1}
									/>
								</li>
							);
						})}
				</ul>
			) : null}
		</NavigableMenu>
	);
}

// eslint-disable-next-line import/prefer-default-export
export { ContentSearch };
